<template>
  <div id="novel-details">
    <div class="breadcrumb-box">
      <Breadcrumb>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/home')"> 首页 </Breadcrumb-item>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/novel')"> 小说管理 </Breadcrumb-item>
        <Breadcrumb-item href="javascript:;" @click.native="routeTo('/novel_chapter', novel.id)"> 章节编辑 </Breadcrumb-item>
        <Breadcrumb-item> 编辑章节 </Breadcrumb-item>
      </Breadcrumb>
    </div>
    <Row>
      <Card class="table-box">
        <h2 class="chapter-title">编辑章节<span>《{{ novel.chinese_title }}》({{ novel.title }})</span></h2>
        <hr>
        <br>
        <i-form ref="formItem" :model="formItem" :label-width="100" :rules="ruleFormItem">
          <Form-item label="章节名(英文)" prop="title">
            <i-input v-model="formItem.title" placeholder="请输入英文名称..." style="width: 500px;"></i-input>
          </Form-item>
          <Form-item label="章节名(中文)" prop="chinese_title">
            <i-input v-model="formItem.chinese_title" placeholder="请输入中文名称..." style="width: 500px;"></i-input>
          </Form-item>
          <Form-item label="章节排序">
            <Input-number :max="999" :min="0" v-model="formItem.order" :step="10"></Input-number>
            <span style="color: #ff5c33;">  tips:1、默认填写0,则为自动排序。2、数值越小,越靠前。</span>
          </Form-item>
          <Form-item label="简介" prop="intro">
            <i-input v-model="formItem.intro" type="textarea" :autosize="{ minRows: 6, maxRows: 8 }" placeholder="简介内容将在首页部分地方显示..."></i-input>
          </Form-item>
          <Form-item label="章节内容" prop="content" class="editor-box">
            <quill-editor ref="myTextEditor"
                          v-model="formItem.content"
                          :config="editorOption"
                          style="height: 260px;">
            </quill-editor>
          </Form-item>
          <Form-item>
            <i-button type="primary" @click.native="handleSubmit('formItem')"> 确认编辑 </i-button>
            <i-button type="ghost" @click.native="routeTo('/novel_chapter', novel.id)" style="margin-right: 10px"> 取消返回 </i-button>
          </Form-item>
        </i-form>
      </Card>
    </Row>
  </div>
</template>

<script>
  import { quillEditor } from 'vue-quill-editor'

  export default {
    data () {
      return {
        novel: {},
        formItem: {
          title: '',
          chinese_title: '',
          intro: '',
          order: 0,
          content: '',
          publish_status: 0
        },
        editorOption: {
          placeholder: '每章内容不建议超过3000字...',
          formula: true,
          modules: {
            toolbar: [
              ['bold', 'italic', 'underline', 'strike'],
              ['blockquote'],
              [{ 'indent': '-1' }, { 'indent': '+1' }],
              [{ 'size': ['small', false, 'large', 'huge'] }],
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
              [{ 'font': [] }],
              [{ 'align': [] }],
              ['clean']
            ]
          }
        },
        ruleFormItem: {
          title: [
            { required: true, message: '英文名称不能为空', trigger: 'blur' },
            { type: 'string', max: 100, message: '书名太长了', trigger: 'blur' }
          ],
          chinese_title: [
            { required: true, message: '中文名称不能为空', trigger: 'blur' },
            { type: 'string', max: 50, message: '书名太长了', trigger: 'blur' }
          ],
          intro: [
            { type: 'string', max: 500, message: '简介不能超过500个字符', trigger: 'blur' }
          ],
          content: [
            { required: true, message: '必须填写章节内容', trigger: 'blur' }
          ],
          publish_status: [
            { required: true, message: '发布状态至少选择一个', trigger: 'change' }
          ]
        }
      }
    },
    created () {
      this.initList()
    },
    components: { quillEditor },
    computed: {
      editor () {
        return this.$refs.myTextEditor.quillEditor
      }
    },
    methods: {
      routeTo (s, novelId) {
        return this.$router.push({ path: s, query: { id: novelId } })
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.formItem.novel_id = this.novel.id
            this.$http.post('admin_api/chapter/edit', this.formItem).then(response => {
              if (response.body.error === 0) {
                this.$Message.success(response.body.message)
                this.$router.push({ path: '/novel_chapter', query: { id: this.novel.id } })
              } else {
                this.$Notice.error({ title: '创建失败', desc: response.body.message })
              }
            })
          } else {
            this.$Message.error('你是不是对提交的信息做手脚了?!')
          }
        })
      },
      onEditorChange ({ editor, html, text }) {
        this.formItem.content = html
      },
      initList () {
        if (!this.$route.query.id || !this.$route.query.novel_id) {
          this.$Notice.error({ title: '缺少小说编号', desc: '你要告诉我那本书哦~' })
          return this.$router.go(-1)
        }
        this.$http.post('admin_api/chapter/edit?get=one', { id: this.$route.query.id }).then(response => {
          if (response.body.error === 0) {
            this.formItem = response.body.data
            this.novel.id = this.$route.query.novel_id
            this.novel.title = response.body.data.novel_title
            this.novel.chinese_title = response.body.data.novel_chinese_title
          } else {
            this.$Notice.error({ title: '初始化数据失败', desc: response.body.message })
          }
        })
      }
    }
  }
</script>

<style>
  .table-box .chapter-title {
    display: block;
    width: 100%;
    height: 32px;
    line-height: 32px;
    overflow: hidden;
  }
  .table-box .chapter-title span {
    font-size: 14px;
    color: #2baee9;
  }
  .editor-box {
    width: 100%;
    height: 300px;
  }
  .editor-box>div.ivu-form-item-content {
    line-height: 24px!important;
  }
</style>



